* {
  font-family: Nunito sans;
  margin: 0;
  padding: 0;
}

// home.jsx
.home-box {
  margin-bottom: 2cm;
}

.main-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1cm;
  padding: 2cm 10vw 3cm 10vw;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    background-image: url("./poster.jpg");
    background-position: center;
    background-size: cover;
    z-index: -1;
    filter: brightness(35%);

  }
}

.left-main-box {
  width: 15cm;
}

.home-buttons {
  display: flex;
  margin-top: .5cm;
  align-items: center;
  // justify-content: center;
  gap: .5cm;


}

.home-button {
  padding: .35cm .8cm;
  border-radius: 50px;
  border: none;
  cursor: pointer;
  font-size: 18px;
}

.helpline {
  border: 3px solid rgb(5, 164, 5);
  background-color: rgb(5, 164, 5);
  // box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  color: white;
  transition: .3s;

  &:hover {
    background-color: transparent;
    backdrop-filter: blur(5px);
  }
}

.donate {
  border: 3px solid royalblue;
  background-color: royalblue;
  // box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  color: white;
  transition: .3s;

  &:hover {
    background-color: transparent;
    backdrop-filter: blur(5px);
  }
}

.tagline {
  font-size: 60px;
  // font-weight: 600;
  font-family: tilt neon;
  color: white;
  line-height: 1.6cm;
}

.left-main-box-text {
  margin-top: .2cm;
  font-size: 18px;
  color: white;
}

.right-main-box {

  flex-shrink: 0;
  width: 11cm;
  height: 11cm;
  border-radius: 500px;
}

.right-img {
  width: 100%;

  flex-shrink: 0;
  border-radius: 500px;
  height: 100%;
}

.home-lower-box {
  padding: 1cm 10vw;
}

.heading {
  font-family: dancing script;
  font-size: 35px;
}

.whatWeDoBoxes {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3cm;
  margin-top: 1cm;
}

.whatWeDoBox {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1cm;

  // border: 1px solid black;
}

.whatWeDoRight {
  width: 9cm;
  height: 9cm;
  border-radius: 500px;
  flex-shrink: 0;
}

.subHead {
  font-family: tilt neon;
  font-size: 40px;
  margin-bottom: .2cm;
}

.subText {
  font-size: 18px;
}

.rescue {
  width: 100%;
  height: 100%;
  border-radius: 500px;
}

.recovery {
  text-align: right;
}

.home-sub-button-box {
  display: flex;
  justify-content: flex-end;
  margin-top: .7cm;
}

.donate-button {
  padding: .3cm .6cm;
  font-size: 16px;
  border: 3px solid royalblue;
  border-radius: 50px;
  background-color: royalblue;
  color: white;
  cursor: pointer;
  transition: .3s;

  &:hover {
    background-color: transparent;
    backdrop-filter: blur(5px);
    color: black;
  }
}

.release-div {
  justify-content: flex-start;
}

.release-button {
  border: 3px solid forestgreen;
  background-color: forestgreen;
}

.healed-hearts-main-box {
  margin-top: 2cm;
}

.healed-hearts-box {
  display: flex;
  justify-content: center;
  gap: .5cm;
  margin-top: 1cm;
}

.healed-heart {
  width: 8cm;
  height: auto;
  border: 1px solid rgb(220, 220, 220);
  border-radius: 10px;
}

.hh-uper {
  width: 100%;
  padding: .2cm;
}

.read-more-stories {
  margin-top: 1cm;
  padding: .4cm 1cm;
  font-size: 16px;
  background-color: forestgreen;
  color: white;
  border: 3px solid forestgreen;
  border-radius: 100px;
  cursor: pointer;
  transform: .3s;

  &:hover {
    background-color: transparent;
    color: rgb(0, 0, 0);
  }
}

.healedImg {
  width: calc(100% - 0.4cm);

  height: auto;
  border-radius: 10px 10px 0px 0px;
}

.hh-lower {
  padding: .2cm .2cm .3cm .2cm;
  background-color: rgb(238, 238, 238);
  border-radius: 0px 0px 10px 10px;
}

.hh-date {
  font-size: 13px;
  font-weight: 600;
  color: blueviolet;
}

.hh-desc {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  -webkit-line-clamp: 3;
  /* Limiting to four lines */
  text-overflow: ellipsis;
}

.hh-watch {
  width: 100%;
  padding: .2cm .5cm;
  font-size: 16px;
  border: 3px solid blueviolet;
  background-color: blueviolet;
  color: white;
  border-radius: 100px;
  margin-top: .5cm;
  cursor: pointer;
  transition: .3s;

  &:hover {
    background-color: transparent;
    color: black;
  }
}

.stats-main-box {
  margin-top: 2cm;
}

.stats-box {
  margin-top: 1cm;
  display: flex;
  justify-content: space-evenly;
  gap: .5cm;
}

.statOne {
  position: relative;
  width: 8cm;
  height: 8cm;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  cursor: pointer;
  // box-shadow: 0px 2  px 8px 3px rgb(223, 188, 255);

  &::before {
    inset: 0;
    content: "";
    z-index: -1;
    filter: brightness(35%);
    border-radius: 50%;
    position: absolute;
    background-image: url("./amb.jpg");
    background-position: center;
    background-size: cover;
    transition: transform .3s ease;
  }

  &:hover::before {
    transform: scale(1.01);
  }
}

.statTwo {
  position: relative;
  width: 8cm;
  height: 8cm;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
