  &::before {
    inset: 0;
    content: "";
    z-index: -1;
    filter: brightness(35%);
    border-radius: 50%;
    position: absolute;
    background-image: url("./shel.jpg");
    background-position: center;
    background-size: cover;
    transition: transform .3s ease;
  }

  &:hover::before {
    transform: scale(1.01);
  }
}

.statThree {
  position: relative;
  width: 8cm;
  height: 8cm;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  cursor: pointer;
  // box-shadow: 0px 2  px 8px 3px rgb(223, 188, 255);

  &::before {
    inset: 0;
    content: "";
    z-index: -1;
    filter: brightness(35%);
    border-radius: 50%;
    position: absolute;
    background-image: url("./staff.jpg");
    background-position: center;
    background-size: cover;
    transition: transform .3s ease;
  }

  &:hover::before {
    transform: scale(1.01);
  }
}

.statText {
  font-size: 30px;
  color: white;
  text-align: center;
  padding: .2cm;
}